import React from 'react';
import { Button, TabBar,NavBar,Icon } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './tblogin.css';
import {connect} from 'react-redux';
import {login} from './login';
const tblogin = (props) => {
    // const [isShow,setShow] = useState(false);
    const log = () => {
        props.dispatch(login())
    }
    return (
        <div>
            <div class="head">淘宝账户登录</div>
            <input  type="text" class="roots"  placeholder="手机号/邮箱/会员名" ></input>
            <input type="password" class="password"  placeholder="请输入登录密码" ></input>
            <div class="divbutton">
                {/* {
                    this.props.username?props.history.goBack(): */}
                <button  class="buttons"
                    onClick={()=>{ log()
                                   props.history.push('/Babytab')}}

                >登录</button>
                    {/* } */}
                </div>    
        </div>
    )
}
const mapStateToProps = (state) => ({
    username: state.username
})
export default connect(mapStateToProps)(tblogin);
