import React from 'react'
import Mytab from './Mytab'
// import './tblogin.css'
import {connect} from 'react-redux'
const Collect = (props) => {
    console.log(props.babydata)
    return (
        <div>
            {props.babydata.map((list,i)=>(
                <div>
                    <img src={list} style={{width:'200px'}}/>

                    <button
                     onClick={()=>{
                        props.dispatch({
                            type:'DELETE',
                            index:i
                        })
                        }}
                        >删除</button>
                </div>            
                ))
            }
            <Mytab/>
        </div>
    )
}
const mapS = (state) => ({
    babydata:state.babydata,
    username: state.login.username,

})
export default connect(mapS)(Collect)
