// 1、搜索框（点击跳转搜索页面，顶部是searchBar，
// 点取消，返回首页）
// 2、轮播图
// 3、商品列表（10个）（点击跳转商品详情页，
// 详情页写上面的图片和返回箭头，点返回箭头返回首页
// 和底部按钮（店铺、客服、收藏、加入购物车和立即购买）
const App = ()=>{
	return <Provider store = {Store}> 
	<Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/home' component={Home} />
			<Route path='/collect' component={Collect} />
			<Route path='/List' component={List} />
			<Route path='/My' component={My} />
			<Route path='/Search' component={Search} />
			<Route path='/Babytab' component={Babytab} />
			<Route path='/Babytab/:id' component={Babytab}/>
			<Route path='/tblogin' component={Tblogin} />


			{/* <Route component={Home}/> */}
			<Route render={()=><Redirect to='/'/>}/>
		</Switch>
	</Router>
	</Provider>
}
export default App;

