import React, { Component } from 'react'
import { BrowserRouter as Router, Redirect, Route,Link,
NavLink} from 'react-router-dom'
import { SearchBar,Carousel,WingBlank, ViewAppRegistry } from 'antd-mobile'
import './home.css';
import Mytab from './Mytab';
import {connect} from 'react-redux'
import {babydata}from './babydata';
import {babydataredux}from './babydataredux';
import Babytab from './Babytab';
 class Home  extends React.Component {
  state = {
    data: ['https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg', 
    'https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg', 
    'https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg',
    'https://gw.alicdn.com/imgextra/i4/128/O1CN01L9c1rK1CofKGrrN4Z_!!128-0-ppp-picassogw.jpg',
    'https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000006276/O1CN01Of18DN1wESL9EdxdP_!!6000000006276-0-octopus.jpg'],
    imgHeight: 176,
  }

  componentDidMount() {
    console.log(1);
    setTimeout(() => {
      this.setState({
        flag:true
      });
    }, 3000);
    this.props.dispatch(babydata());
  }
  render() {
    // console.log(this.props.dispatch(babydata()));
    // console.log(this.props.babydata);
    return ( 
       
      <div>
        <Link to='/Search'>
          <div className='searchbar'> 
          <img src={require(`./img/taobao.png`)}/>
          <SearchBar  placeholder="寻找宝贝店铺" onclick={this.handleClick} style={{width:'100%',float:'right',backgroundColor:'rgb(255,133,42)'}}>
        </SearchBar>
        </div>
        </Link> 
        <Carousel
          autoplay={true}
          infinite
          // beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          // afterChange={index => console.log('slide to', index)}
        >
          {this.state.data.map(val => (
            <a
              key={val}
              href="http://www.taobao.com"
              style={{ display: 'inline-block', width: '100%', height: this.state.imgHeight }}
            >
              <img
                // src={'https://img.alicdn.com/tfs/TB1FjZ7VWL7gK0jSZFBXXXZZpXa-520-280.png'}
                src={`${val}`}
                alt=""
                style={{ width: '100%',height:'200px', verticalAlign: 'top'}}
                onLoad={() => {
                  // fire window resize event to change height
                  window.dispatchEvent(new Event('resize'));
                  this.setState({ imgHeight: 'auto' });
                }}
              />
            </a>
          ))}
        </Carousel>
        <div style={{textAlign:'center'}}>
        <img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png" style={{width: '177px',height:'20px',marginTop:'20px'}}></img>
        </div>
        <WingBlank>
        <div  className='bobys'>
          <ul>
            {this.props.babydata.map((data)=>{
              return <li>
                <img src={data.img}
                onClick={()=>{
                this.props.history.push(
                  {
                    pathname:'/Babytab',
                    src:data.img
                  })}}/>
                <div className='txt'> 
                <p>{data.title}</p>
                <span class='money'>{data.price}</span>
              </div>
                
              </li>
                    })}
          <div style={{clear:'both'}}></div>
          </ul>
          <div style={{height:'50px'}}></div>
        </div>    
      </WingBlank>
      <Mytab/>
      <ul className="recommend-container">
        
      </ul>
    </div>
    );   
  } 
  // routerTo(v) {
  //   this.props.history.push({pathname: `/Babytab/${v.id}`, state: {data: v}})
  // }
}
const mapStateToProps = (state) =>({
  babydata:state.babydataredux.babydata
})

export default connect(mapStateToProps)(Home);


